/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
@import url("https://fonts.googleapis.com/css?family=Lora:400,400i,700,700i|Playfair+Display:400,400i,700,700i,900,900i");
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

html {
  height: 100%;
  box-sizing: border-box; }

body {
  line-height: 1;
  min-height: 100%; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

img {
  max-width: 100%; }

/* unvisited link */
a:link {
  color: inherit; }

/* visited link */
a:visited {
  color: inherit; }

/* mouse over link */
a:hover {
  color: inherit; }

/* selected link */
a:active {
  color: inherit; }

a {
  transition: 0.1s linear; }

/*  ==========================================================================
      breakpoint-max -
      This mixin allows us to use specific breakpoints
      defined in our variables, and generate the correct
      (max-width) media query required
  =========================================================================== */
body {
  font-family: "Lora", serif;
  color: #333; }

h1,
h2,
h3,
h4,
h5 {
  font-family: "Playfair Display", serif;
  color: #000; }

.container {
  margin-left: auto;
  margin-right: auto;
  padding-left: 5px;
  padding-right: 5px; }
  @media (min-width: 768px) {
    .container {
      max-width: 1440px;
      padding-left: 70px;
      padding-right: 70px; } }
  .container-outter {
    margin-left: auto;
    margin-right: auto; }
    @media (min-width: 768px) {
      .container-outter {
        max-width: 1440px;
        padding-left: 120px;
        padding-right: 120px; } }
  .container-fixed {
    position: fixed;
    width: 100%;
    z-index: 999;
    bottom: 0; }
    @media (min-width: 768px) {
      .container-fixed {
        bottom: unset; } }

.title__underline {
  border-bottom: solid 1px #000;
  padding: 0 0 10px; }

.title--md {
  font-size: 1.4rem; }

.title__sm-only {
  display: inline-block; }
  @media (min-width: 768px) {
    .title__sm-only {
      display: none; } }

.title__md-only {
  display: none; }
  @media (min-width: 768px) {
    .title__md-only {
      display: block; } }

.hl {
  color: #fd885d; }

.header {
  padding-top: 6%;
  padding-bottom: 6%; }
  .header__title {
    margin-left: 50%;
    transform: translateX(-50%); }

.top-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-top: 1px solid #f0f0f0;
  background-color: #fff; }
  @media (min-width: 768px) {
    .top-bar {
      justify-content: space-between;
      padding: 0;
      border: none;
      background-color: unset; } }
  .top-bar__title {
    text-transform: capitalize;
    font-size: 1.1rem; }
  .top-bar__toggle {
    width: 20px;
    cursor: pointer;
    transition: ease 0.15s; }
    .top-bar__toggle:hover {
      opacity: 0.4; }
  .top-bar .menu__icons {
    margin-top: 0; }
    .top-bar .menu__icons__icon {
      width: 18px;
      margin-right: 10px; }

.menu {
  font-size: 50px;
  position: fixed;
  background-color: #fff;
  top: 0;
  left: 0;
  bottom: 0;
  overflow: scroll;
  transform: translatex(-35%);
  transition: transform 300ms cubic-bezier(0.5, 0, 0, 1), opacity 200ms 0ms linear, visibility 0ms 350ms linear, margin 0ms 350ms linear;
  opacity: 0;
  visibility: hidden;
  overflow-y: scroll;
  overflow-x: hidden;
  z-index: 50; }
  .menu__nav {
    border-bottom: 1px solid #f0f0f0;
    padding-bottom: 2rem;
    padding-top: 2rem;
    transition: ease 0.15s; }
    .menu__nav__active {
      color: #fd885d !important; }
    @media (min-width: 768px) {
      .menu__nav {
        border-bottom: 0;
        padding-top: 0;
        padding-bottom: 0; } }
    .menu__nav a {
      font-size: 1.4rem;
      text-decoration: none;
      color: #333;
      text-transform: capitalize;
      display: block;
      padding-top: 0.8rem;
      padding-bottom: 0.8rem;
      transition: ease 0.15s; }
      .menu__nav a:hover {
        color: #fd885d; }
  .menu__about {
    padding-top: 3.2rem; }
    @media (min-width: 768px) {
      .menu__about {
        padding-top: 0; } }
  .menu__about, .menu__nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; }
    @media (min-width: 768px) {
      .menu__about, .menu__nav {
        margin-top: 0;
        width: 50%;
        height: 100vh;
        border-right: 1px solid #f0f0f0; } }
  .menu__about {
    border-top: 1px solid #f0f0f0; }
    @media (min-width: 768px) {
      .menu__about {
        border-top: 0; } }
    .menu__about__inside {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column; }
  @media (min-width: 768px) {
    .menu__container {
      display: flex;
      justify-content: space-between; } }
  .menu__title {
    font-size: 1.1rem; }
  .menu__desc {
    font-size: 1.1rem;
    text-align: center;
    line-height: 1.5;
    max-width: 80%; }
    @media (min-width: 1024px) {
      .menu__desc {
        max-width: 343px; } }
  .menu__avatar {
    border-radius: 50%;
    width: 120px; }
  .menu__icons__icon {
    width: 25px;
    margin-right: 20px;
    transition: ease 0.15s; }
    .menu__icons__icon:hover {
      opacity: 0.4; }
  .menu__title, .menu__desc, .menu__icons {
    margin-top: 1.2rem; }

.active-menu .menu {
  transition: transform 250ms cubic-bezier(0.42, 0, 0.12, 1), opacity 100ms linear, visibility 0ms 0ms linear;
  width: 100%;
  transform: translatex(0);
  opacity: 1;
  visibility: visible; }
  @media (min-width: 768px) {
    .active-menu .menu {
      width: 70%; } }

.active-menu .header__title {
  display: none; }

.active-menu .top-bar .title--md {
  display: none; }

.front {
  display: flex; }
  .front__card {
    width: 30%; }

.footer {
  padding-top: 3.2rem;
  margin-top: 4rem;
  padding-bottom: 3.2rem;
  border-top: 1px solid #f0f0f0; }
  .footer__inside {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center; }
  .footer__icons {
    margin-top: 0;
    width: 18px; }
    .footer__icons a {
      margin-right: 10px; }
  .footer__links, .footer__icons, .footer__note {
    display: flex;
    margin-bottom: 3.2rem; }
  .footer__links {
    display: flex;
    justify-content: center;
    flex-wrap: wrap; }
    .footer__links a {
      display: block;
      padding: 0.4rem 1.2rem;
      color: #aaa; }
      @media (min-width: 768px) {
        .footer__links a {
          padding: 0 1.2rem; } }
      .footer__links a:hover {
        color: #333; }
  .footer__note a, .footer__links a {
    text-decoration: none; }
